@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../styles/common';

$width: 96px;
$height: 96px;

.Key {
    position: relative;
    width: $width;
    height: $height;
    margin: $spacing-01;

    &__inner {
        background: $color-itvx-neutrals-500;
        color: $color-itvx-text-primary;
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        padding: 0;
        border: 0;
        border-radius: 0;
        box-sizing: content-box;
        @include itv-font-type(subtitle-2);
        min-width: $width;
        text-transform: none;

        &:hover,
        &.focus {
            font-weight: 700;
            border: none;
            background: $color-itvx-neutrals-100;
            box-shadow: 0 2px 4px 3px rgba(0, 0, 0, 0.5);
            color: $color-itvx-text-inverse;
        }

        &--tertiary {
            background: $color-itvx-background-tertiary;
            color: $color-itvx-text-primary;
        }

        &--neutral {
            background: $color-itvx-neutrals-500;
            color: $color-itvx-text-primary;
        }
    }

    // Rules for different types of keys
    &--backspace {
        $icon-width: 60px;
        $icon-height: 39px;

        i.cp_icon__backspace {
            width: $icon-width;
            height: $icon-height;
        }

        .focus {
            i.cp_icon__backspace {
                width: calc(#{$icon-width} * 1.2);
                height: calc(#{$icon-height} * 1.2);
            }
        }

        button {
            width: 196px;
        }
    }

    &--space,
    &--spaceWider {
        $icon-width: 59px;
        $icon-height: 17px;

        i.cp_icon__space {
            width: $icon-width;
            height: $icon-height;
        }

        .focus {
            i.cp_icon__space {
                width: calc(#{$icon-width} * 1.2);
                height: calc(#{$icon-height} * 1.2);
            }
        }
    }

    &--clear,
    &--space,
    &--backspace,
    &--symbols,
    &--lowercase {
        width: 2 * $width + 2;
    }

    &--spaceWider {
        width: 4 * $width + 9;
    }
}
