@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

.AccountPassword {
    &__message {
        margin-top: $spacing-03;
        @include itv-font-type(body-large, bold);
        color: $color-itvx-text-secondary;
    }

    &__btnGroup {
        margin-top: $spacing-06;
        margin-bottom: $spacing-06;
        display: flex;
        justify-content: center;
        min-height: 45px;
    }

    &__btn {
        margin-right: $spacing-04;
        text-align: center;
        padding: 0 $spacing-06;

        &:last-child {
            margin-right: unset;
        }

        &:only-child {
            margin-right: $spacing-04;
        }

        &--notLink {
            box-sizing: content-box;
        }
    }
}
