@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../VodControls';
@import '~@itv/fe-components/cjs/common/styles/tokens/variables';

.ParentalButton {
    @extend %VodControls__supplementaryButton;

    margin: 0;

    .fade-text {
        width: 0;
    }

    &.focus {
        color: $color-itvx-text-inverse;
        width: 260px;
        padding-left: $spacing-04;

        .fade-text {
            width: 100%;
        }
    }

    &__icon {
        height: 25px;
        width: 18px;
    }

    &__button {
        &--on-state {
            border-bottom: 5px solid $color-itvx-brand-primary;

            &.focus {
                background-color: $color-itvx-brand-primary;
                border: 1px solid $color-itvx-brand-primary;
                color: $color-itvx-text-inverse;

                svg {
                    g {
                        fill: $color-itvx-text-inverse;
                    }
                }
            }
        }
    }
}
