@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/common';

$slider-margin-left: 43px;

.Slider {
    margin-bottom: $spacing-07;
    margin-left: $slider-margin-left;
    position: relative;
    z-index: 1;

    .cp_slider__heading,
    .cp_slider__container {
        padding: 0;
        margin: 0;
    }

    .cp_slider__container {
        overflow: initial;
    }

    .cp_slider__heading {
        margin-left: $focus-border-width * 2;
        margin-bottom: $spacing-05;
    }

    .Link {
        display: block;
    }

    ul.cp_slider__list {
        .animations & {

            transition: all $motion-duration-01 $motion-easing-04;
            transform-origin: top;

            .Slider--no-transition & {
                transition: none;
            }
        }

        li.cp_slider__list-item {
            width: min-content;
            white-space: normal;
            padding: 0;

            &:last-child .Tile {
                // this is to allow the slider to continue scrolling left
                // for the last three slides.
                padding-right: 800px;
            }
        }
    }

    h2,
    .cp_heading-with-icon {
        @include itv-font-type(subtitle-2);
        line-height: 1.25 !important; /* stylelint-disable-line itv/typography -- override line-height for focus frame and letterbox alignment */ 
    }

    &__container {
        display: flex;
    }

    .hidden &, &__hide {
        display: none;
    }
}