@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

.HubPlusNotificationModal {
    .warning-icon {
        width: 112px;
        height: 93.5px;
        margin-top: $spacing-08;
        padding-top: $spacing-05;
        margin-bottom: $spacing-09;

        path:first-of-type {
            fill: $color-itvx-functional-warning;
        }

        path:last-of-type {
            fill: transparent;
            stroke: $color-itvx-functional-warning;
        }
    }

    svg + h2 {
        margin-top: 0;
    }

}
