@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/common';

.Page {
    position: relative;
    display: flex;
    flex-direction: row;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    background-color: $color-itvx-background-primary;

    &__content {
        height: calc(100% - #{$safe-area-bottom});
        max-height: $app-height;
        width: 100%;
        position: relative;
    }
}

#EmailVerification, #upgradePage{
    background: linear-gradient(90deg, $color-itvx-background-secondary 25%, rgba(1, 63, 86, 1) 90%);
}

// When a modal is displayed via the ModalPortal, the Page should be hidden.
// This is a work around for a rendering engine bug where parts of the main nav
// and page are rendered whilst the modal is displayed, FED-2570.
.App--has-modal {
    .Page {
        display: none;
    }
}
