@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/variables';
@keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: $opacity-06;
    }
}

.videoParent {
    &.is-finished {
        visibility: hidden;
    }
}

.VodPlayer {
    width: $app-width;
    height: $app-height;
    transform: translateZ(0);

    &__digital-onscreen-graphic {
        position: absolute;
        top: 30px;
        left: 50px;
        max-width: 90px;
    }

    .ScrollableRows {
        height: 460px;
    }

    .FocusFrame.is-slider {
        top: 39px;
        left: 4px;
        height: 142px;
    }
}
