@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../styles/common';

// Page level overrides
.Page.Upsell {
    background: linear-gradient(90deg, $color-itvx-background-secondary 25%, rgba(1, 63, 86, 1) 90%);
    
