@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

.OnwardRecsTileSlider {
    position: absolute;
    bottom: 32px; // spacing-07
    left: 0;
    z-index: 100;

    .cp_tile-slider__container {
        padding-left: 96px;
    }

    .cp_tile-slider__heading {
        padding-left: 96px;
        opacity: $opacity-06;
    }

    .cp_tile__body-wrapper {
        height: $onward-recs-tile-height;
    }

    .cp_tile__body-wrapper,
    .cp_tile-slider__list-item {
        max-width: $onward-recs-tile-width;
    }
}
