@import '../../../../styles/common';

$player-control-width: $spacing-08;

.VodControls,
%VodControls {
    display: flex;
    flex-direction: row;
    width: 100%;
    margin-top: $spacing-04;
    margin-bottom: $spacing-10;

    :nth-child(4) {
        margin-left: auto;
    }

    button[disabled] {
        opacity: $opacity-01;
        border: 1px solid transparent;
    }

    &__rewind,
    &__fastForward {
        min-width: $player-control-width;
        box-shadow: none;
        border: 0;
        margin: 0;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;

        .cp_icon {
            height: 12px;
            width: 20px;
        }

        &.focus {
            background-color: $color-itvx-background-inverse;
            border: 0;
            outline: 0;

            svg {
                filter: drop-shadow(0 3px 3px rgba(0, 0, 0, 0.3));

                path {
                    fill: $color-itvx-text-inverse;
                }
            }
        }
    }

    &__playPause {
        margin: 0 $spacing-05;
        box-shadow: none;
        min-width: $player-control-width;
        border: 0;
        display: flex;
        align-items: center;
        justify-content: center;

        .cp_icon {
            width: 15px;
            height: 20px;
        }

        &.focus {
            background-color: $color-itvx-background-inverse;
            border: 0;
            outline: 0;

            svg {
                g {
                    fill: $color-itvx-text-inverse;
                    stroke: $color-itvx-text-inverse;

                    path {
                        stroke: $color-itvx-text-inverse;
                    }
                }
            }
        }
    }

    &__subtitles,
    &__audioDescription,
    &__supplementaryButton {
        min-width: $player-control-width;
        outline: 0;
        border: 0;
        transition: opacity 0s;
        transition: width $motion-duration-02 ease-out;
        background-color: rgba($color-itvx-neutrals-100, 0.1);
        overflow: hidden;

        /*
    * There's a rendering issue where the transparency on the background colour
    * causes some weird artifacts on some TV's when the element is over a video.
    * This little hack moves the element to it's own layer and forces the
    * rendering to the graphics card, solving the issue.
    */
        transform: translateZ(0);

        &.focus {
            background-color: $color-itvx-background-inverse;
            color: $color-itvx-text-inverse;
            border: 0;
            outline: 0;

            .fade-text {
                display: inline-block;
                transition: opacity $motion-duration-02 ease-out $motion-duration-01;
            }
        }

        &--active {
            &::after {
                content: '';
                height: 4px;
                width: $player-control-width;
                position: fixed;
                margin: $spacing-05-5 0;
                border-radius: 0 0 $radius-02 $radius-02;
                background-color: $color-itvx-brand-primary;
            }

            &.focus {
                background-color: $color-itvx-background-inverse;
                border: 1px solid $color-itvx-brand-primary;
                color: $color-itvx-text-inverse;
            }
        }

        .fade-text {
            display: none;
            font-size: initial;
            font-weight: 400;
            text-align: center;
            white-space: nowrap;
        }
    }

    &__audioDescription {
        @include itv-font-type(subtitle-1, bold);
        justify-content: center;
        align-items: center;
        margin-left: 0;
        text-transform: uppercase;

        .cp_icon {
            height: 32px;
            width: 32px;
        }

        &.focus {
            width: 248px;
            padding-left: $spacing-03;

        }

        .fade-text {
            padding: 0 $spacing-03 0 $spacing-03;
            flex: 1;

        }

        &--on {
            border-bottom: 5px solid $color-itvx-brand-primary;

            &.focus {
                background-color: $color-itvx-brand-primary;
                border: 1px solid $color-itvx-brand-primary;
                color: $color-itvx-text-inverse;
            }
        }
    }

    &__noAudioDescription {
        position: absolute;
        top: -5000px;
    }
}
