@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../styles/common';

.PinPairing {
    &__divider {
        position: absolute;
        height: 1000px;
        width: 1000px;
        opacity: $opacity-05;
        border-radius: 1.85%;
        transform: translate(99%, 10%) rotate(302deg) skewX(333deg) scale(2);
        border: 3px solid $color-itvx-text-interactive;
        box-shadow: inset 4px 4px 250px rgba(0, 0, 0, 0.8);
        pointer-events: none;
    }

    &__container {
        display: flex;
        align-items: flex-end;
        position: relative;
        z-index: 10;
        margin-left: 150px;
    }

    &__item {
        &:first-of-type {
            margin-right: 400px;
        }
    }

    &__logo {
        width: 225px;
        color: $color-itvx-brand-primary;
        margin-bottom: 91px;
    }

    &__img {
        margin-bottom: $spacing-09;
    }

    &__heading {
        @include itv-font-type(title-2);
        font-weight: 700;
        margin-bottom: $spacing-09;
    }

    &__item:last-child {
        position: relative;
        top: -8px;
    }

    &__instructions {
        margin-top: $spacing-09;

        > p {
            max-width: 365px;
        }

        p {
            @include itv-font-type(subtitle-2);
            color: $color-itvx-text-secondary;
            margin: 0;
        }
    }

    &__link {
        color: $color-itvx-text-primary;
    }

    &__list {
        padding-left: 1.7em;
        @include itv-font-type(subtitle-2);
        margin: 0 0 $spacing-06;

        li {
            margin-bottom: $spacing-05;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    &__loginCode {
        @include itv-font-type(large-title);
        color: $color-itvx-light-text-interactive;
    }

    &__footerBtn {
        margin-top: $spacing-09;
    }
}
