@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/common';
@import '../../styles/variables';

$tile-width: 252px;

.Tile {
    display: block;
    margin: 0;
    min-width: $tile-width;
    padding: 0 $spacing-02;

    h3 {
        @include itv-font-type(body);
        margin-top: $spacing-03;

        &.cp_basic-tile__title {
            font-weight: 700;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            display: block;
            width: 249px;
        }

        .focus & {
            text-decoration: none;
            color: $color-itvx-brand-primary;
        }
    }

    &.hidden > .cp_basic-tile {
        display: none;
    }

    .cp_picture__img {
        width: $tile-width;

        .focus & {
            z-index: 3;
        }
    }

    .cp_basic-tile__body.cp_spacing--03-top {
        margin-top: -$spacing-02;
    }

    .cp_basic-tile__picture-wrapper {
        border: 3px solid transparent;
        position: relative;
    }

    .cp_basic-tile__body header {
        margin-left: $spacing-02;
    }

    &--portrait{
        min-width: $portrait-tile-width;

        .cp_portrait-tile__picture-wrapper {
            border: 3px solid transparent;
            position: relative;
        }

        .cp_picture__img {
            width: $portrait-tile-width;

            .focus & {
                z-index: 3;
            }
        }
    }
}
