@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import "../../../../styles/common";

.ParentalGuidance {
    .Takeover__border {
        padding: $spacing-07;
        max-width: 624px;

        .DialogButtons {
            margin-top: 0;
        }
    }

    .Takeover__container {
        max-width: 624px;
    }

    .Takeover__footer {
        padding: $spacing-04 $spacing-05;
    }

    .FocusGroup--row:nth-of-type(2) {
        margin-top: $spacing-04;
    }

    .DialogButtons {
        button {
            width: 235px;
        }
    }

    &__OptionButton {
        color: $color-itvx-text-secondary;
        margin-top: $spacing-07;
        @include itv-font-type(body-large);

        .cp_icon {
            height: 25px;
            width: 18px;
        }

        .Takeover__container {
            max-width: 600px;
        }

        .FocusGroup--row:nth-of-type(2) {
            margin-top: $spacing-04;
        }
    }

    &__header {
        svg {
            height: 96px;
            width: 68px;
        }

        h1 {
            margin: $spacing-07 0;
        }
    }
}
