@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
.ChannelLogo {
    display: inline-block;
    height: 18px;

    &--itv {
        width: 35px;
    }

    &--itv2,
    &--itv3 {
        width: 44px;
    }

    &--itv4 {
        width: 41px;
    }

    &--itvbe {
        width: 62px;
    }

    &--citv {
        width: 45px;
    }
}
