@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '~@itv/fe-components/cjs/common/styles/tokens/variables';
@import '../../../../styles/common';

.SimControls {
    margin-top: $spacing-04;
    margin-bottom: $spacing-05;
    display: inline-flex;
    flex-direction: row;
    justify-content: space-between;

    .SubtitlesButton {
        margin-right: 0;
    }

    &__button_container {
        margin-right: $spacing-04;
    }

    &__controls_container {
        display: inline-flex;
    }

    &__control_button {
        height: 40px;

        &--back {
            width: 40px;
            padding: 0;
            display: flex;
            justify-content: center;
            align-items: center;

            .cp_icon {
                width: 12px;
            }
        }

        &--start_again {
            display: flex;
            justify-content: center;
            align-items: center;
            height: 40px;

            &.focus {
                font-weight: 400;
            }

            .cp_icon {
                width: 18px;
                margin-right: $spacing-05;
            }
        }
    }
}
