@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

.KidsErrorModal {
    &__container {
        display: flex;
        box-sizing: border-box;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 487px;
        height: 302px;
        padding: $spacing-07 $spacing-09;
        background: $color-itvx-background-secondary;
        border-radius: $radius-03;
        text-align: center;
    }

    &__header {
        @include itv-font-type(title-1);
        padding-bottom: $spacing-06;
    }

    &__text {
        @include itv-font-type(body-large);
        color: $color-itvx-text-secondary;
        padding-bottom: $spacing-06;

        &--bold {
            font-weight: 700;
        }
    }

    &__button {
        width:174px;
        height: 50px;
    }
}
