@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/common';

.PINEntry {
    display: flex;
    flex: 0 1 auto;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    align-items: center;

    &__entryField {
        margin-bottom: $spacing-06;
        width: 365px;
        justify-content: center;

        .EntryField__caret {
            margin-left: $spacing-08;
        }

        &:not(.EntryField--placeholder) {
            span {
                margin-right: -0.8em;
            }
        }
    }

    &__error {
        color: $color-itvx-functional-error;
        text-transform: uppercase;
        margin-bottom: $spacing-06;
        @include itv-font-type(body-small);
    }

    .cp_button {
        padding: $spacing-04 $spacing-05;
        min-width: 190px;
    }
}
