@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';
$button-width: 240px;

.TimedButton {
    padding: $spacing-04 0;
    height: 50px;
    text-align: center;
    position: relative;
    overflow: hidden;
    box-sizing: border-box;
    width: $button-width;
    @include itv-font-type(cta);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: rgba($color-itvx-brand-primary, 0.7);
    border-radius: $radius-02;
    will-change: auto;

    &__progress {
        background-color: rgba($color-itvx-brand-primary, 1);
        position: absolute;
        left: 0;
        top: 0;
        width: $button-width;
        height: 50px;
        will-change: transform;
        transform: translateX(-$button-width);
        transition-property: transform;
        transition-timing-function: $motion-easing-04;

        &.start {
            transform: translateX(0);
        }
    }
}