@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../styles/common';

$notch-path: "data:image/svg+xml,%3Csvg width='48' height='139' viewBox='0 0 48 139' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M48 0H0V139H48V132.324L7.59392 75.2766C5.14111 71.8136 5.14111 67.1797 7.59392 63.7167L48 6.66978V0Z' ";

.MainNavLink {
    @include itv-font-type(cta);
    padding: 0 0 0 $spacing-03;
    height: 32px;
    box-sizing: border-box;
    display: flex;
    margin-right: 0;
    position: relative;
    justify-content: flex-start;
    align-items: center;
    color: $color-itvx-neutrals-300;
    margin-bottom: $spacing-08;

    &__label {
        display: none;
    }

    &__activeIcon {
        display: none;
    }

    &__inactiveIcon {
        display: inline-block;
    }

    &:hover {
        .cp_icon {
            color: $color-itvx-brand-primary;
        }
    }

    &--selected {
        color: $color-itvx-neutrals-300;
        text-decoration: none;

        .cp_icon {
            color: $color-itvx-brand-primary;
        }

        .activeIcon {
            display: inline-block;
        }

        .inactiveIcon {
            display: none;
        }
    }

    &.focus {
        color: $color-itvx-text-primary;
        font-weight: 700;

        &::before,
        &::after {
            content: '';
            position: absolute;
            background-color: $color-itvx-background-secondary;
            border-right: 3px solid $color-itvx-brand-primary;
            height: 100vh;
            right: -40px;
            display: none;
            z-index: 3;
        }

        &::after {
            width: 46px;
            top: 84px;
        }

        &::before {
            width: 46px;
            bottom: 80px;
        }

        .MainNavLink__label {
            display: block;
            position: relative;
            width: 100%;

            &::after,
            &::before {
                content: '';
                display: none;
                position: absolute;
                height: 139px;
                width: 48px;
                background-size: 100%;
                background-repeat: no-repeat;
                top: -54px;
            }

            &::after {
                right: -37px;
                z-index: 2;
                content: url("#{$notch-path} style='fill: %23102533;'/%3E%3C/svg%3E");
            }

            &::before {
                right: -40px;
                z-index: 1;
                content: url("#{$notch-path} style='fill: %23deeb52;'/%3E%3C/svg%3E");
            }
        }
    }

    .inactiveIcon,
    .activeIcon,
    .cp_icon__settings {
        max-width: 28px;
        min-width: 28px;
        min-height: 28px;
        max-height: 28px;
    }

    &__label,
    &__label-profile {
        opacity: $opacity-06;
        z-index: 2;
        white-space: nowrap;
        margin-left: $spacing-06;
        text-transform: uppercase;
    }
}
