@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

.PinProtect {
    z-index: 99;

    &__container {
        display: flex;
        background: $color-itvx-background-primary;
        border-radius: $radius-03;
        flex-direction: column;
        align-items: center;
        box-sizing: border-box;
        width: 640px;
        padding: $spacing-09;
        text-align: center;
        position: relative;
    }

    &__icon {
        width: 68px;
        height: 96px;
        margin-bottom: $spacing-07;
    }

    &__header {
        @include itv-font-type(title-1);
        margin-bottom: $spacing-07;
    }

    &__text {
        @include itv-font-type(body-large);
        margin-bottom: $spacing-07;
    }

    &__button {
        text-transform: uppercase;

        &:first-child {
            margin-right: $spacing-05;
        }
    }
}
