@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../styles/common';
@import 'components/Link/Link';

.SignInManual {
    .StandardEntry {
        margin: unset;
    }

    &__container {
        position: relative;
        display: flex;
        flex-direction: column;
        padding: $spacing-10;
        height: 100%;
        justify-content: center;
        align-items: center;
    }

    &__userEmail {
        @include itv-font-type(body-large);
        position: absolute;
        top: 10px;
    }

    &__btnGroup {
        margin-top: $spacing-07;
        margin-bottom: $spacing-07;
        display: flex;
        justify-content: center;
    }

    &__btn {
        margin-right: $spacing-04;

        &:last-child {
            margin-right: 0;
        }
    }

    &__link {
        color: $color-itvx-text-primary;
        font-weight: 700;
    }

    &__message {
        @include itv-font-type(body-large);
        color: $color-itvx-text-secondary;
    }
}