@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../styles/common';

.ChannelsGrid {
    overflow: hidden;
    position: absolute;
    z-index: 3;
    display: block;

    &__header {
        @include itv-font-type(subtitle-2, bold);
        margin: 0 0 $spacing-04 $spacing-02;
    }

    &__row {
        display: flex;
        flex-direction: row;

        .ProgrammeTile {
            margin-right: $spacing-05;
        }
    }

    .Link.focus {
        .cp_basic-tile__picture-wrapper {
            border: 3px solid $color-itvx-brand-primary;
        }
    }

    .FocusFrame {
        display: none;
    }
}
