@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../styles/common';

.PrivacyPolicyTakeover {
    width: 100%;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;

    &__container {
        width: 687px;
    }

    &__title {
        @include itv-font-type(large-title);
        margin: 0 0 $spacing-06;
    }

    &__question {
        @include itv-font-type(subtitle-2);
        margin-bottom: $spacing-07;
    }

    &__button {
        margin: 0 $spacing-05;
    }
}
