@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '~@itv/fe-components/cjs/common/styles/tokens/variables';

@keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: $opacity-06;
    }
}

.LoadingBar {
    &__wrapper {
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        svg {
            opacity: 0;
            animation: fadein $motion-duration-06;
            animation-delay: $motion-duration-06;
            animation-fill-mode: forwards;
        }
    }
}
