@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../styles/common';

$overlay-buttons-max-width: 658px;

.OverlayButtons {
    display: flex;
    align-items: stretch;
    justify-content: center;

    width: 100%;
    max-width: $overlay-buttons-max-width;
    margin-bottom: $spacing-08;

    &.FocusGroup--row {
        button:not(:only-child) {
            flex-basis: 100%;
        }

        button + button {
            margin-left: $spacing-04;
        }
    }

    &.FocusGroup--column {
        flex-direction: column;

        button {
            width: 100%;
        }

        button + button {
            margin-top: $spacing-04;
        }
    }
}
