@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/variables';

.GuidanceNotification {
    &__container {
        position: relative;
        display: flex;
        justify-content: flex-end;
    }

    &__guidance {
        margin: $spacing-08;
        background-color: $color-itvx-neutrals-500;
        padding: $spacing-04 $spacing-05;
        border-radius: $radius-02;

        .cp_guidance__icon {
            width: 24px;
            height: 24px;
            padding-right: $spacing-01;
        }
    }
}
