@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../styles/variables';

.CookiesPolicyOnboarding {
    background: $background-linear-gradient;
    padding: $spacing-12 $app-padding-right 0 $spacing-08-5;
    height: calc(100% - #{$app-padding-top});
    width: $app-width;
    overflow: hidden;
    position: relative;
    left: 192px;

    &__focusGroup {
        height: 100%;
    }

    .cp_grid,
    .cp_grid__item {
        height: 100%;
    }

    &__button {
        min-width: 100px;
    }
}
