@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../styles/common';

.CookiesConsent {
    background: $background-linear-gradient;
    width: 100%;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;

    &__container {
        width: 990px;

        p {
            @include itv-font-type(subtitle-1);
            color: $color-itvx-text-primary;
            margin: 0 auto;
            margin-bottom: $spacing-07;
        }
    }

    &__title {
        margin: 0 0 $spacing-07;
        @include itv-font-type(large-title);
    }

    &__copy--bold {
        font-weight: 700;
    }

    &__button {
        margin: 0 $spacing-04;
    }
}
