@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/common';

$controls-height: 216px;
$controls-button-size: 66px;
$controls-margin-top: $spacing-05;
$content-wrapper-padding-vertical: 20px;

.ScrollableText {
    display: flex;
    flex-direction: column;
    height: 80%;

    &__contentWrapper {
        position: relative;
        padding: $spacing-09;
        background: $color-itvx-background-tertiary;
        overflow: hidden;
        flex: 1;
    }

    &__content {
        position: relative;
        height: 100%;
        margin-right: $spacing-07;
        overflow: hidden;

        table {
            table-layout: fixed;

            td {
                padding: $spacing-04;
                vertical-align: baseline;
                width: 50%;
            }
        }
    }

    &__controls {
        margin-top: $spacing-07;
        height: $controls-height;
    }

    &__buttonsContainer {
        display: flex;
        flex-wrap: wrap;
    }

    &__button {
        width: $controls-button-size;
        height: $controls-button-size;
        padding: 0;
        margin-right: $spacing-05;
        min-width: unset;

        &:hover {
            svg g {
                fill: $color-itvx-text-inverse;
            }
        }

        &.focus {
            svg g {
                fill: $color-itvx-text-inverse;
            }
        }
    }

    &__additionalButtons {
        flex-grow: 1;
        text-align: right;
    }

    h2,
    h3 {
        margin: 0 0 $spacing-04;
        @include itv-font-type(subtitle-1);
    }

    p {
        margin: 0 0 $spacing-04;
        @include itv-font-type(body-large);
        color: $color-itvx-text-primary;
    }
}
