@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/common';

.SubNav {
    padding-top: 127px;
    background: $color-itvx-background-secondary no-repeat;
    background-size: 35%;
    width: 0;
    overflow: hidden;
    opacity: 0;
    flex: 0 0 auto;

    &:not(.SubNav--loading) {
        &.FocusGroup--containsFocus {
            opacity: $opacity-06;
            width: 220px;
            left: 242px;
            position: relative;
            background: none;
            z-index: 1;
        }
    }

    > * {
        white-space: nowrap;
    }

    ul,
    &__item {
        margin: 0;
        padding: 0;
        text-indent: 0;
        list-style-type: none;
        position: relative;
    }

    &__title {
        margin: $spacing-06 $spacing-07;
        @include itv-font-type(subtitle-1);
        color: $color-itvx-text-primary;
    }

    &__link {
        display: block;
        width: calc(100% - 41px);
        padding: $spacing-04 $spacing-04 $spacing-04 $spacing-07;
        color: $color-itvx-neutrals-200;
        @include itv-font-type(body-large);

        &--has-icon {
            padding: $spacing-05 $spacing-04 $spacing-05 $spacing-07;
        }

        .cp_icon {
            height: 20px;
            transform: scale(1);
            margin-left: 0;
            transition: all $motion-duration-01;
        }

        path {
            fill: $color-itvx-text-secondary;
        }

        &--disabled {
            color: $color-itvx-neutrals-300;
            opacity: $opacity-03;
            pointer-events: none;
        }

        &:hover {
            background-color: $color-itvx-background-inverse;
            color: $color-itvx-text-inverse;

            path {
                fill: $color-itvx-text-inverse;
            }
        }

        &.focus {
            text-decoration: none;
            color: $color-itvx-text-inverse;
            background-color: $color-itvx-brand-primary;
            font-weight: 700;

            &:hover {
                background-color: none;
                border: 0;
            }

            .cp_icon {
                transform: scale(1.25);
                margin-left: $spacing-02;
            }

            path {
                fill: $color-itvx-text-inverse;
            }
        }
    }
}

.animations {
    .SubNav {
        transition: all $motion-duration-01 ease-out;
    }
}
