@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '~@itv/fe-components/cjs/common/styles/tokens/variables';
@import '../../../../styles/common';



.SubtitlesButton {
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    min-width: 40px;
    outline: 0;
    border: 0;
    transition: opacity 0s;
    transition: width $motion-duration-02 ease-out;
    background-color: rgba($color-itvx-neutrals-100, 0.1);
    overflow: hidden;

    .cp_icon {
        height: 22px;
        width: 22px;
    }

    .fade-text {
        @include itv-font-type(subtitle-1);
        display: none;
        font-size: initial;
        text-align: center;
        white-space: nowrap;
    }


    &.focus {
        width: 164px;
        background-color: $color-itvx-background-inverse;
        color: $color-itvx-text-inverse;
        border: 0;
        outline: 0;
        padding-left: $spacing-03;

        .fade-text {
            display: inline-block;
            transition: opacity $motion-duration-02 ease-out $motion-duration-01;
            flex: 1;
        }
    }

    &--active {
        &::after {
            content: '';
            height: 4px;
            width: 40px;
            position: fixed;
            margin: $spacing-05-5 0;
            border-radius: 0 0 $radius-02 $radius-02;
            background-color: $color-itvx-brand-primary;
        }

        &.focus {
            background-color: $color-itvx-background-inverse;
            border: 1px solid $color-itvx-brand-primary;
            color: $color-itvx-text-inverse;
        }
    }

    &--on {
        border-bottom: 5px solid $color-itvx-brand-primary;

        &.focus {
            color: $color-itvx-text-inverse;
            background-color: $color-itvx-brand-primary;
            border: 1px solid $color-itvx-brand-primary;
        }

    }

}
