@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../../styles/common';
@import '~@itv/fe-components/cjs/common/styles/library/reset';

@include reset-button('PlayerFrame');

$playerframe-border-width: 3px;

.PlayerFrame {
    position: absolute;
    box-sizing: content-box;
    height: 274px;
    width: 486px;
    background-color: transparent;
    top: calc(#{$spacing-07} - #{$playerframe-border-width});
    right: calc(96px - #{$playerframe-border-width});

    &.focus {
        margin: 0;
        border: $playerframe-border-width solid $color-itvx-brand-primary;
        outline: none;
    }
}