@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/common';

.Hero {
    position: relative;
    height: calc(601px - #{$app-padding-top});
    z-index: 1;
    background-color: $color-itvx-background-primary;

    .heroFullBackground {
        width: $app-width;
        height: auto;
    }

    .cp_picture {
        display: block;
        overflow: hidden;
    }

    &.UpgradeHero,
    &.EmailVerificationHero {
        height: 100%;

        &.heroFullBackground {
            top: 0;
        }
    }

    &__content-wrapper {
        bottom: 0;
        height: 100%;
        left: 0;
        position: relative;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        z-index: 4;
    }

    &__content {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
    }

    &__image {
        z-index: 2;
        position: absolute;
        top: -$app-padding-top;
        right: -$app-padding-right;
        bottom: 0;
        left: 0;
    }

    &__left-gradient {
        position: absolute;
        background-color: $color-itvx-background-primary;
        top: -$app-padding-top;
        left: -46px;
        bottom: 0;
        width: calc(586px + 46px);
        z-index: 3;
    }

    &__bottom-gradient {
        position: absolute;
        background-color: $color-itvx-background-primary;
        height: 70px;
        left: -46px;
        bottom: 0;
        right: -70px;
        z-index: 3;
    }
}
