@use "sass:math";
@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';

@import '~@itv/fe-components/cjs/common/styles/settings/misc';
@import '../../styles/common';
@import '../../styles/variables';


.FocusFrame {
    box-shadow: $effect-itvx-focus-glow;
    border: 2px solid $color-itvx-border-focus;
    border-radius: $radius-03;
    position: absolute;
    z-index: 3;

    &.is-episode {
        width: $episode-width;
        height: $episode-height;
    }

    &.is-slider {
        top: 38px;
        left: 47px;
        width: 252px;
        height: 141px;
    }

    &.is-banner {
        width: $episode-width; 
        height: $episode-height;
    }

    &.is-hero-slider {
        display: none;
    }

    &.is-portrait-slider {
        top: 43px;
        left: 47px;
        width: $portrait-tile-width;
        height: #{$portrait-tile-width * math.div(1, $itv-portrait-aspect-ratio)};
    }

    &.hidden {
        display: none;
    }
}
