@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import "components/OverlayModal/OverlayModal";

.FailedLoginModal {
    background-color: rgba($color-itvx-background-secondary, 0.85);

    .OverlayModal__content {
        max-width: 714px;
        padding: 0;
    }

    &__topWrapper {
        background-color: $color-itvx-background-primary;
        padding: $spacing-07 $spacing-09;

        > h2 {
            @include itv-font-type(title-1);
            margin-bottom: $spacing-07;
        }

        > p.cp_paragraph {
            @include itv-font-type(body-large);
            margin-bottom: $spacing-07;
        }

        .OverlayButtons {
            padding-top: 0;
        }

        &--text {
            @include itv-font-type(body-small);
        }
    }

    &__tryAgainBtn {
        max-width: 201px;
    }

    &__footer {
        background-color: $color-itvx-background-tertiary;
        padding: $spacing-06 $spacing-07;
        margin-bottom: 0;

        // Nested footerText. We need it nested as OverlayModal has too specific styling in it's own .scss file
        p.FailedLoginModal__footerText {
            margin-bottom: 0;
        }
    }
}
