@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '~@itv/fe-components/cjs/common/styles/tokens/variables';
@import 'styles/z-index';
@import '../../styles/common';


@keyframes fade-out {
    0% {
        opacity: $opacity-06;
    }

    100% {
        opacity: 0;
    }
}

.splashVideo {
    position: fixed;
    right: 0;
    bottom: 0;
    min-width: 100%; 
    min-height: 100%;
}

.LoadingScreen {
    position: fixed;
    @include z-index(loadingScreen);
    background-repeat: no-repeat;
    background-size: 0;
    background-color: $color-itvx-background-secondary;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;

    &--fadeOut {
        animation-name: fade-out;
        animation-duration: $motion-duration-06;
        animation-fill-mode: forwards;
        animation-timing-function: cubic-bezier(0, 0.8, 0.5, 1);
    }

    &--appUnavailable {
        animation-duration: 0s;
    }

    &__sponsorship {
        position: absolute;
        left: 1486px;
        top: 908px;
        background-image: none;

        img {
            width: 135px;
        }

        .cp_sponsorship__text {
            @include itv-font-type(body-small);
        }
    }
}

.amazonfiretv,
.browser,
.samsung {
    .LoadingScreen {
        background-size: contain;
        @media screen and (min-width: 1920px) {
            background-size: 70%;
        }
    }
}
