@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/common';

@keyframes PulseAttention {
    50% {
        background: $color-itvx-brand-primary;
    }
}

.EntryField {
    display: flex;
    flex-direction: row;
    position: relative;
    overflow: hidden;
    padding: $spacing-06 0;
    @include itv-font-type(title-2);
    min-height: 48px;

    &--placeholder {
        color: $color-itvx-neutrals-100;
    }

    &__caret {
        width: 1px;
        height: 48px;
        margin-left: $spacing-04;
        animation: PulseAttention $motion-duration-06 cubic-bezier(.215, .61, .355, 1) forwards infinite;
    }

    &::after {
        position: absolute;
        bottom: 0;
        content: '';
        left: 0;
        opacity: $opacity-03;
        height: 1px;
        width: 100%;
        border-bottom: 2px solid $color-itvx-neutrals-300;
    }
}
