@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

.SimMetadata {
    h1 {
        @include itv-font-type(title-1, bold);
    }

    .cp_metadata {
        padding: 0 0 $spacing-07 $spacing-03;

        .cp_icon__guidance {
            width: 24px;
            height: 24px;
        }

        .cp_guidance__paragraph {
            @include itv-font-type(body-small);
            max-width: 1150px;
            white-space: pre;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }

    .cp_episode-info {
        @include itv-font-type(body-small);
        padding: $spacing-04 0;

        &__series-hierarchy {
            @include itv-font-type(body-small, bold);
        }

        .cp_icon {
            line-height: 1rem; /* stylelint-disable-line itv/typography -- override line-height for icon alignment */
            height: 100%;
        }

        &__channel-logo {
            max-height: 100%;
            width: 48px;
        }
    }
}
