@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import "../../../styles/common";

.ProfileButton {
    .Button:hover {
        border: none;
    }

    .Button.focus,
    .Button:hover,
    .Button {
        background-color: transparent;
    }

    .Button.focus .ProfileButton__avatar {
        box-shadow: 0 0 0 $spacing-03 $color-itvx-background-inverse;
    }

    .cp_button {
        text-transform: none;
        padding: 0;
    }

    .cp_avatar {
        .cp_icon {
            position: absolute;
        }
    }
}

.ProfileButton {
    &__button {
        border: none;
    }
}
