@import 'shared';
@import '../subtitles/cues';

.sky {
    @include video;
    @include player-overlay;
}

// temporary use of Zagma for subtitles as the only installed font on the
// Sky device is similar to Comic Sans which is not great for accessibility / subtitles
::cue {
    font-family: zagma, Arial, sans-serif;
}

video::-webkit-media-text-track-display {
    @media (max-width: 360px) {
        font-size: 150%; /* stylelint-disable-line itv/typography -- cannot be replicated with itv-font-type */
    }
}
