@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../../styles/common';

.PlayerSlider {
    padding-bottom: $spacing-08;
    padding-left: $spacing-08;
    position: relative;
    z-index: 1;

    .Tile {
        padding-top: $spacing-05;

        &:last-child {
            // this is to allow the slider to continue scrolling left
            // for the last three slides.
            padding-right: 800px;
        }
    }

    &--featured {
        .Tile {
            min-width: 339px;

            img {
                width: 100%;
            }
        }
    }

    &__header {
        width: 100%;
        margin: 0 0 0 $spacing-03;
        @include itv-font-type(subtitle-2);

        &.cp_section-header {
            display: block;
        }
    }

    &__container {
        display: flex;
    }

    &.hidden {
        display: none;
    }
}
