@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/common';

$modal-content-max-height: 1058px;
$modal-content-width: 738px;

.OverlayModal {
    position: fixed;
    width: $app-width;
    height: $app-height;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    @include z-index(modal);
    background-color: rgba($color-itvx-background-primary, 0.9);
    display: flex;
    align-items: center;
    justify-content: center;

    &__content {
        filter: drop-shadow(0 5px 20px rgba(0, 0, 0, 0.33));
        margin: auto;
        width: $modal-content-width;
        max-width: $modal-content-width;
        word-wrap: break-word;
        max-height: $modal-content-max-height;
        padding: 0;
        border-radius: $radius-03;
        color: $color-itvx-text-secondary;
        text-align: center;
        background-color: $color-itvx-background-primary;
        overflow: hidden;
        display: flex;
        flex-direction: column;
        align-items: center;

        h2 {
            @include itv-font-type(title-1);
            margin-top: $spacing-08;
        }

        p, h2 {
            max-width: 658px;
            margin-bottom: $spacing-07;
        }
    }
}
