@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../styles/variables';

$hero-height: 803px;

.HomePreview {
    position: relative;
    height: 340px;

    &__hero {
        opacity: 0;
    }

    &--hero {
        height: $hero-height;

        .HomePreview__hero {
            opacity: $opacity-06;
        }
    }

    &--hero-tile {
        height: 130px;
        position: relative;
    }

}

.animations {
    .HomePreview--animate {
        transition: height $motion-duration-02;
        transform-origin: top;

        .HomePreview__hero-tile,
        .HomePreview__hero {
            transition: opacity $motion-duration-01 ease-in;
            transform-origin: top;
        }

        .HomePreview__hero {
            transition-delay: 0s;
        }
    }
}
