@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/common';

.Sponsorship {
    background-image: linear-gradient(210deg, $color-itvx-background-primary, rgba(21, 24, 36, 0) 47%); // bespoke
    height: 190px;
    padding: $spacing-07 $spacing-07 0 160px;
    position: absolute;
    right: 0;
    top: 0;
    @include z-index(sponsorship);

    &.hidden {
        opacity: 0;
    }

    .cp_heading {
        color: $color-itvx-text-primary;
        @include itv-font-type(body-small);
        font-weight: 700;
        margin-left: $spacing-02;
        position: relative;
    }

    img {
        width: 120px;
    }
}

.animations {
    .Sponsorship {
        transition: opacity $motion-duration-01;
    }
}
