@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '~@itv/fe-components/cjs/common/styles/tokens/variables';

.AdMarker {
    background-color: $color-itvx-background-inverse;
    position: absolute;
    list-style-type: none;
    border-radius: $radius-01;
    top: 0;
    width: 4px;
    height: 100%;

    &--expired {
        background-color: $color-itvx-background-secondary;
        width: 6px;
    }

    &__text {
        visibility: hidden;
    }
}
