@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import "../../styles/common";

.AppUnavailableModal {
    background-color: $color-itvx-background-tertiary;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    width: 600px;
    color: $color-itvx-text-primary;
    font-weight: 400;

    &__title {
        @include itv-font-type(title-1);
        padding: $spacing-07 $spacing-07 $spacing-05;
        margin: 0 0 $spacing-04;
    }

    &__message {
        @include itv-font-type(subtitle-1);
        padding: 0 $spacing-07;
        margin: 0;
    }

    &__buttonGroup {
        width: 300px;
        margin-left: $spacing-14;
        margin-top: $spacing-07;
        margin-bottom: $spacing-07;
        text-align: center;
    }

    &__button {
        padding: $spacing-02;
        width: 180px;
        height: 40px;
        border-radius: 0;
        @include itv-font-type(cta);
        font-weight: 400;

        &.focus {
            background-color: $color-itvx-background-inverse;
            border: none;
            font-weight: 400;
        }
    }
}
