@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../styles/common';

$header-max-width: 846px;
$icon-list-margin-top: 218px;

.HeroUpsell {
    background: linear-gradient(90deg, $color-itvx-background-secondary 25%, rgba(1, 63, 86, 1) 90%);
    height: auto;
    width: auto;

    .Hero__content-wrapper {
        transform: translateY(0);
        top: 0;
    }

    .Hero__image {
        right: unset;
        top: 0;
        bottom: unset;
    }

    &__container {
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        padding-top: 122px;
    }

    &__header {
        @include itv-font-type(featured, bold);
        margin-top: $spacing-06;
        text-transform: uppercase;
        max-width: $header-max-width;
        font-style: italic; /* stylelint-disable-line itv/typography -- no need to reset font-type here */

        &--basic {
            @include itv-font-type(featured);
        }

        &--highlighted {
            @include itv-font-type(featured, bold);
            color: $color-itvx-brand-primary;
        }
    }

    &__details {
        @include itv-font-type(subtitle-1);
        text-transform: uppercase;
        margin-top: $spacing-03;

        &--highlighted {
            color: $color-itvx-brand-primary;
            text-transform: uppercase;
            font-weight: 700;
        }
    }

    &__pricing {
        @include itv-font-type(body);
        color: $color-itvx-text-secondary;
        margin-top: $spacing-05;
        margin-right: $spacing-05;
    }

    &__buttonsWrapper {
        display: flex;
        margin-top: $spacing-06;
    }

    &__buttonContainer {
        display: flex;
        align-items: space-between;
        flex-direction: column;

        &:first-child {
            margin-right: $spacing-05;
            min-width: 324px;
        }
    }

    &__bestValueTag {
        width: 319px;
        color: $color-itvx-accents-primary;
        border: 1px solid $color-itvx-accents-primary;
        border-radius: $radius-01;
        box-shadow: 0 2px 6px 3px rgba(21, 24, 36, 0.2); // TODO: token for this?
        text-transform: uppercase;
        @include itv-font-type(body);
        text-align: center;
        margin-top: $spacing-04;
    }

    &__benefitsWrapper {
        margin-top: $icon-list-margin-top;
        width: 100%;
    }
}
