@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '~@itv/fe-components/cjs/common/styles/tokens/variables';


.Home__content {
    .ContinueWatchingSlider {
        padding-left: 0;
        margin-left: -$spacing-03;

        .ProgrammeTile {
            min-width: unset;
        }

        .Tile {
            min-width: 252px;

            img {
                width: 100%;
            }
        }

        .focus {
            position: relative;

            &::before {
                content: '';
                position: absolute;
                z-index: 2;
                border: 3px solid $color-itvx-brand-primary;
                width: 255px;
                height: 143px;       
                left: 4px;
            }
        }
    }
}
