@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../styles/common';

.FooterUpsell {
    &__focus-group {
        margin-top: $spacing-08;
        display: flex;
        align-items: center;
    };

    &__text {
        margin-right: $spacing-06;
        @include itv-font-type(title-3);
    }

    &__button {
        margin-right: $spacing-05;
        @include itv-font-type(cta);

        &.focus {
            font-weight: 400;
        }
    };

    &__terms {
        color: $color-itvx-neutrals-300;
        @include itv-font-type(body);
        margin: $spacing-08 0 0;
        width: 100%;

        &--content {
            color: $color-itvx-neutrals-300;
            @include itv-font-type(body);
            margin-top: $spacing-08;
            margin-bottom: 0;
        }

        &--highlighted {
            color: $color-itvx-brand-primary;
        }

        &--copyright {
            color: $color-itvx-neutrals-300;
            margin-top: 0;
            @include itv-font-type(body);
        }
    }
}
