@import 'styles/variables';

@mixin video {
    #av-player,
    video {
        object-fit: fill;
        width: 100%;
        height: $app-height;
        max-height: 100%;
        position: absolute;
        left: 0;
        top: 0;
    }
}

@mixin player-overlay {
    .PlayerOverlay {
        padding-left: 0;
    }
}

@mixin scale-down {
    .LoadingScreen {
        background-size: contain;
        transform: scale(1);
    }
    @media screen and (max-width: 1919px) {
        transform-origin: 0 0;
        transform: scale(calc(2 / 3));

        .LoadingScreen {
            background-size: 100%;
        }
    }
}

@mixin slider-fix {
    // Channels page

    .ChannelsContent {
        .RecentlyOnSlider {
            .ProgrammeTile {
                min-width: 347px;
            }
        }
    }
}

@mixin slider-fix-top-picks {
    // This code is to fix the first row's CSS (Top picks) of the
    // Categories page as the slider fix is for the first row's CSS
    // (Recently On) of the Channels page
    // In some devices the width of the elements is not calculated correctly
    // and with this fix the elements are correctly displayed

    .CategoryContent {
        .AbTopPicksSlider {
            .ProgrammeTile {
                min-width: 347px;
            }
        }
    }
}
