@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../styles/common';

$continue-watching-tile-width: 252px;

.ContinueWatchingTile {
    min-width: $tile-width;

    &__progress.cp_progress-indicator {
        margin-top: $spacing-02;
        margin-left: $spacing-02;
        width: $continue-watching-tile-width;
    }

    &__title {
        margin-top: $spacing-03;
        margin-left: $spacing-02;
    }

    &__next-episode-label {
        @include itv-font-type(body, bold);
        color: $color-itvx-brand-primary;
        margin: #{-$spacing-01} 0 #{-$spacing-03} $spacing-02;
    }

    &__time-left.cp_time-left {
        @include itv-font-type(body-small);
        color: $color-itvx-neutrals-300;
        margin-left: $spacing-02;
    }

    &__date-time,
    &__date-time.cp_series-hierarchy {
        @include itv-font-type(body-small);
        color: $color-itvx-neutrals-300;
        margin-left: $spacing-02;
    }
}
