@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

$panel-height: 520px;

.PlayerOverlay {
    background: $background-vertical-gradient;
    height: $panel-height;
    padding-top: $spacing-11;
    left: 0;
    right: 0;
    bottom: -$panel-height;
    position: absolute;
    box-sizing: border-box;
    z-index: 1;
    display: flex;
    justify-content: flex-end;
    flex-flow: column;
    opacity: $opacity-06;
    transition-property: transform, opacity;
    transition-timing-function: cubic-bezier(0.51, 0.01, 0.41, 1), $motion-easing-04;
    transition-duration: $motion-duration-03, $motion-duration-03;

    &--visible {
        opacity: $opacity-06;
        transform: translateY(-$panel-height);
    }

    .FocusFrame.is-slider {
        top: 142px;
        left: 54px;
    }

    &__gradient {
        width: $app-width;
        height: 48px;
        opacity: $opacity-05;
        position: absolute;
        bottom: 0;
        margin: 0 0 0 -$spacing-08-5;
    }

    #vodControlsRow {
        padding-top: $spacing-06;
    }

    #moreEpisodesRow {
        padding-top: $spacing-03;
    }
}
