@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

.ParentalControlsInformation {
    .Takeover__border {
        padding: $spacing-09;
    }

    .Takeover__title {
        @include itv-font-type(title-1);
        width: 425px;
        margin: 0 auto;
    }

    &__container {
        margin: 0 auto;
    }

    &__text {
        @include itv-font-type(body);
        width: 450px;
        margin-top: $spacing-07;
    }

    &__button {
        margin-top: $spacing-07;
        min-width: 110px;
    }
}
