@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

.SimOverlay {
    height: 390px;
    padding-top: 0;
    background: $background-vertical-gradient;

    &__content {
        margin: 0 $spacing-08-5;
        position: relative;
        height: 390px;
    }

    &__controls {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }

    .LiveTVSlider {
        margin-left: -$spacing-03;
        padding-left: 0;

        .Slider__header {
            padding-left: $spacing-02;
        }

        .focus .LiveTVTile__title {
            color: $color-itvx-brand-primary;
        }
    }

    .FocusFrame.is-slider {
        top: 36px;
        left: -3px;
        height: 142px;
    }

    .BottomNav {
        margin-top: $spacing-05;
    }
}
