@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/common';
@import '~@itv/fe-components/cjs/common/styles/tokens/variables';

.StandardEntry {
    display: flex;
    flex: 0 1 auto;
    flex-direction: column;
    margin: auto;
    position: relative;

    &__entryField {
        margin-bottom: $spacing-07;
        box-shadow: none;
        background: none;
        padding: $spacing-06;
        @include itv-font-type(title-2);

        &.EntryField--placeholder {
            color: $color-itvx-neutrals-300;
            font-weight: 400;
        }

        &::after {
            background-image: none;
            background-color: $color-itvx-background-inverse;
        }
    }

    &__inputIcon {
        display: flex;
        height: 96px;
        position: absolute;
        right: 0;
        padding-left: $spacing-04;
        padding-right: $spacing-04;
        align-items: center;

        // Stop it jumping when changing to show icon
        .cp_icon__show-input {
            top: 3px;
        }

        svg {
            width: 62px;
            height: 48px;
        }
    }

    &__entryField {
        margin-bottom: $spacing-06;
    }
}
