@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
/* stylelint-disable -- deprecated component */
@import '../../../styles/common';

.FocusButton {
  background-color: transparent;
  border: 1px solid $color-itvx-neutrals-300;
  color: $color-itvx-text-primary;
  @include itv-font-type(body-large);
  border-radius: 5px;
  padding: $spacing-04 $spacing-06;
  cursor: pointer;
  outline: none;
  min-width: 154px;

  &.focus {
    background-color: $color-itvx-brand-primary;
    border: 1px solid $color-itvx-brand-primary;
    color: $color-itvx-text-inverse;
    font-weight: 700;
  }

  &:hover {
    background-color: $color-itvx-background-inverse;
    border: 1px solid $color-itvx-background-inverse;
    color: $color-itvx-text-inverse;
  }

  &:disabled {
    opacity: 0.4;
    background-color: transparent;
    color: $color-itvx-text-primary;
    &:hover {
      background-color: none;
      cursor: not-allowed;
    }
  }

  &--no-border {
    border: none;

    &.focus {
      border: none;
    }
  }

  &--with-icon {
    display: inline-flex;
    align-items: flex-end;

    i {
      padding-left: $spacing-03;
      padding-right: $spacing-03;
    }
  }
}
