@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

.OnwardJourney {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    padding: $spacing-07 96px;
    box-sizing: border-box;
    border-radius: $radius-02;
    background-color: $color-itvx-background-secondary;
    display: flex;
    flex-direction: column;

    &__title,
    &__info,
    &__synopsis {
        width: 800px;
        z-index: 100;
    }

    &__title {
        margin-bottom: $spacing-05;
        @include itv-font-type(title-2, bold);
    }

    &__synopsis {
        margin-bottom: $spacing-07;
        @include itv-font-type(body);
        @include text-wrap(body, 2);
        max-width: 620px;
    }

    &__picture {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 0;
    }

    &__horizontal-gradient {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 960px;
        background: linear-gradient(
            270deg,
            rgba($color-itvx-background-primary, 0) 1.35%,
            rgba($color-itvx-background-primary, 0.6) 48.05%,
            $color-itvx-background-primary 97.75%
        );
        z-index: 50;
    }

    &__vertical-gradient {
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        height: 442px;
        width: 100%;
        background: linear-gradient(
            360deg,
            $color-itvx-background-primary 0%,
            rgba($color-itvx-background-primary, 0.67) 79.77%,
            rgba($color-itvx-background-primary, 0) 100%
        );
        z-index: 50;
    }

    &__timerButton {
        align-self: start;
        min-width: 360px;
        z-index: 100;
    }

    &__watchNowButton {
        align-self: start;
        z-index: 100;
    }
}
