@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

$osn-height: 44px;
$osn-margin-width: 4px;

.NotAvailableOSN {
    position: absolute;
    top: 100px;
    right: 20px;
    min-width: 155px;
    height: $osn-height;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        height: $osn-height;
        width: $osn-margin-width;
        background-color: $color-itvx-functional-warning;
    }

    &__notification-container {
        position: absolute;
        display: flex;
        flex-direction: row;
        width: fit-content;
        height: $osn-height;
        left: $osn-margin-width;
        background: $color-itvx-background-secondary;
    }

    &__icon-container {
        position: relative;
        width: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: $spacing-01;
    }

    &__text-container {
        position: relative;
        display: flex;
        align-items: center;
        @include itv-font-type(body);
        color: $color-itvx-text-primary;
        font-weight: 400;
        margin: $spacing-04 $spacing-03 $spacing-04 $spacing-02;
    }
}
