@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/common';

.KidsOnboarding {
    position: relative;
    width: 100%;
    height: 100%;
    background-size: 100%;
    background-repeat: no-repeat;
    background-color: $color-itvx-background-primary;

    &__content {
        display: flex;
        align-items: flex-end;
        justify-content: center;
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 100;
    }

    &__wrapper {
        display: flex;
        text-align: center;
        flex-direction: column;
        align-items: center;
        height: 100%;
        margin-bottom: $spacing-10;
    }

    &__logo-container {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 332px;
        height: 203px;
    }

    &__itvxLogo {
        color: $color-itvx-brand-primary;
        width: 248px;
    }


    &__button {
        &:first-of-type {
            margin-right: $spacing-05;
        }
    }

    &__textContainer {
        color: $color-itvx-text-primary;
        text-shadow: 0 3px 6px rgba(0, 0, 0, 0.5);
    }

    &__title {
        @include itv-font-type(large-title);
    }

    &__subTitle {
        margin-top: $spacing-06;
        @include itv-font-type(subtitle-1);
        color: $color-itvx-text-secondary;
    }

    &__bulletsContainer {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    &__list {
        display: inline-block;
        text-align: justify;
        margin: 0;
        padding: 0 0 0 $spacing-04;
    }

    &__listItem {
        @include itv-font-type(body);
        list-style-position: outside;
        margin-top: $spacing-06;
    }

    &__buttonContainer {
        margin-top: $spacing-08;
    }

    &::after {
        content: '';
        position: absolute;
        right: 0;
        left: 0;
        bottom: 0;
        background: linear-gradient(180deg, rgba($color-itvx-background-primary, 0), $color-itvx-background-primary 94.18%);
        width: 100%;
        height: 715px;
        z-index: 99;
    }
}
