@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import "../../styles/common";

.LocationForm {
    display: flex;
    flex-direction: column;

    &__body {
        display: flex;
    }

    &__disclaimer {
        background-color: rgba($color: $color-itvx-neutrals-400, $alpha: 0.2);
        border-radius: $radius-02;
        padding: 86px 66.75px;
        text-align: center;

        &__wrapper {
            margin-left: $spacing-09;
            min-width: 447px;
            max-width: 447px;
        }

        h2 {
            @include itv-font-type(subtitle-1, bold);
        }

        p {
            margin-top: $spacing-07;
            color: $color-itvx-neutrals-200;
        }
    }

    &__status {
        min-height: 42px;
        height: 42px;
        @include itv-font-type(subtitle-1, bold);
        text-align: center;
        margin-bottom: $spacing-09;
        margin-top: $spacing-06;

        .cp_icon {
            margin-right: $spacing-05;
            height: 28px;
            width: 28px;
        }

        &__recognised {
            color: $color-itvx-functional-success;
        }

        &__unrecognised {
            color: $color-itvx-functional-error;
        }
    }

    &__buttonGroup {
        margin-top: $spacing-07;
        text-align: center;

        button {
            margin-right: $spacing-05-5;

            &:last-child {
                margin-right: 0;
            }
        }
    }

    h1 {
        @include itv-font-type(large-title);
        margin-bottom: $spacing-11;
        text-align: center;
    }
}
