@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import "../../styles/common";
@import "../../styles/variables";

.ProfileSelection {
    @include z-index(takeover);
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    display: flex;
    justify-content: center;
    width: $app-width;
    height: $app-height;
    max-width: $app-width;
    max-height: $app-height;
    background: $gradient-itvx-radial-primary;

    &__content {
        margin-top: 292px;
        display: flex;
        align-items: center;
        flex-direction: column;
    }

    &__title {
        @include itv-font-type(large-title);
        margin-bottom: $spacing-10;
    }

    &__takeover {
        background: $color-itvx-background-primary;
    }

    &__buttons {
        display: flex;
        flex-direction: row;
        gap: 84px;

        .cp_button {
            padding: 0;
        }
    }
}
