// Reset
//
// Import reset file within component
// @import "..styles//library/reset"
//
// Use required mixin(s) for each element
// @include reset-link('link');
// @include reset-button('link');
// @include reset-list('link');

// Anchor
// @include reset-link('link');
//
@import '../helpers/variables';

@mixin reset-link($selector) {
    .#{$selector} {
        &:focus {
            outline: thin dotted;
        }
    }
}

// Button
// @include reset-button('button');
@mixin reset-button($selector) {
    .#{$selector} {
        padding: 0;
        background-color: transparent;
        border: none;
        outline: none;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        border-radius: 0;

        &:focus {
            outline: thin dotted;
        }
    }
}

// Image
// @include reset-image('image');
@mixin reset-image($selector) {
    .#{$selector} {
        max-width: 100%;
        vertical-align: top;
    }
}

// Input
// @include reset-input('input');
@mixin reset-input($selector) {
    .#{$selector} {
        width: 100%;
        padding: 0;
        // iOS - Safari specific CSS
        -webkit-appearance: none;
        border-radius: 0;

        &:disabled {
            opacity: $opacity-06;
        }
    }
    .#{$selector}[type="date"],
    .#{$selector}[type="search"] {
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
    }
}

// List
// @include reset-list('list');

@mixin reset-list($selector) {
    .#{$selector} {
        padding: 0;
        margin: 0;
        list-style: none;
    }
}

// Select
// @include reset-select('select');
@mixin reset-select($selector) {
    .#{$selector} {
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        appearance: none;
        border-radius: 0;
        width: 100%;
    }
    .#{$selector}::-ms-expand {
        /* for IE 11 */
        display: none;
    }
}
