@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/common';

.Takeover {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    @include z-index(takeover);
    height: 100%;

    &--no-image {
        .Takeover__overlay {
            background: $color-itvx-background-primary;
        }
    }

    &__overlay {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;

        &--background {
            background: linear-gradient(0deg, rgba(21, 24, 36, 1) 0%, rgba(21, 24, 36, 1) 20%, rgba(21, 24, 36, 0.3) 94%); // bespoke
        }
    }

    &__header,
    &__footer {
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: $color-itvx-background-tertiary;
        @include itv-font-type(body-small);
        height: 43px;
        padding: $spacing-04 0;
    }

    &__header {
        padding: $spacing-04 $spacing-06;
        border-radius: $radius-03 $radius-03 0 0;

        .Takeover__inner {
            max-width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        .cp_icon {
            display: block;
            margin-right: $spacing-03;
            flex-shrink: 0;
        }
    }

    &__footer {
        border-radius: 0 0 $radius-03 $radius-03;
    }

    &__border {
        padding: $spacing-07;
        background-color: $color-itvx-background-secondary;
        text-align: center;

        &--no-background {
            background: none;
        }
    }

    &__border--radius {
        border-radius: $radius-03;
    }

    &__title {
        margin-bottom: $spacing-07;
        @include itv-font-type(title-1);
        margin-top: 0;
    }

    &--opacityBackground {
        background-color: rgba($color-itvx-background-primary, 0.9);
    }

    &__container {
        box-shadow: 0 3px 6px 4.5px rgba(0, 0, 0, 0.5);
        border-radius: $radius-03;

        &--no-shadow {
            box-shadow: none;
        }
    }
}

// Override overlay bg for signin. Can remove when Takeover/Overlay component updated
.SignIn.Takeover--no-image {
    .Takeover__overlay {
        background: linear-gradient(180deg, #102533 0%, #01374B 34.38%);
    }
}
