@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

.ScrubBar {
    padding: 0 0 $spacing-04 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: $spacing-06;
    height: 21px;

    &__total-bar {
        width: 100%;
        height: 10px;
        align-self: center;
        border-radius: $radius-02;
        position: relative;
        background-color: rgba($color-itvx-neutrals-300, 0.19);

        /*
    * There's a rendering issue where the transparency on the background colour
    * causes some weird artifacts on some TV's when the element is over a video.
    * This little hack moves the element to it's own layer and forces the
    * rendering to the graphics card, solving the issue.
    */
        transform: translateZ(0);

        &--extended {
            width: 1186px;
        }
    }

    &__time-remaining {
        @include itv-font-type(body-small);
        width: $scrub-bar-time-remaining-width;
        text-align: right;
        margin-left: $spacing-05;

        &--hidden {
            display: none;
        }
    }

    &__elapsed-time {
        position: absolute;
        @include itv-font-type(body-small);
        padding-top: $spacing-04;

        &--hidden {
            opacity: 0;
        }
    }

    &__elapsed-bar {
        height: 10px;
        background-color: $color-itvx-functional-success;
        border-radius: $radius-02;
    }

    &__playhead-container {
        width: 100%;
        display: flex;
        justify-content: flex-end;
        padding-bottom: $spacing-05;

        &--playhead {
            width: 34px;
            height: 34px;
            border-radius: 50%;
            background: $color-itvx-background-inverse;
            position: absolute;
            top: -12px;
            margin-right: -$spacing-07;
        }

        &--hidden {
            display: none;
        }
    }

    &__elapsed-time-vod {
        color: $color-itvx-brand-primary;
    }

    &__elapsed-time-simulcast {
        color: $color-itvx-functional-success;
    }

    &__elapsed-bar {
        height: 10px;
        border-radius: $radius-02;
    }

    &__elapsed-bar-vod {
        background-color: $color-itvx-brand-primary;
    }

    &__elapsed-bar-simulcast {
        background-color: $color-itvx-functional-success;
    }

    &__playhead-container {
        width: 100%;
        display: flex;
        justify-content: flex-end;
        padding-bottom: $spacing-05;

        &--playhead {
            width: 34px;
            height: 34px;
            border-radius: 50%;
            background: $color-itvx-background-inverse;
            position: absolute;
            top: -12px;
            margin-right: -$spacing-07;
        }

        &--hidden {
            display: none;
        }
    }

}
