@import 'shared';

.xbox {
    @include video;
    @include player-overlay;
    @include slider-fix;
    @include slider-fix-top-picks;

    // Fix for Xbox One because width: min-content; is not supported on Edge 18
    .Slider {
        ul.cp_slider__list {
            li.cp_slider__list-item {
                width: auto;
            }
        }
    }
}
