@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';
@import "components/OverlayModal/OverlayModal";
@import '~@itv/fe-components/cjs/common/styles/library/typography';

.IncorrectPasswordModal {
    .OverlayModal__content {
        padding: 0;
        width: 648px;
        max-width: 648px;
    }

    &__topWrapper {
        padding: $spacing-09 $spacing-09 $spacing-07 $spacing-09;
    }

    &__footer {
        background-color: $color-itvx-background-tertiary;
        padding: $spacing-06 $spacing-09;
        margin-bottom: 0;
        white-space: pre-wrap;

        p {
            margin: 0;
        }

        &--text {
            @include itv-font-type(body-small);

            &--bold {
                font-weight: 700;
                color: $color-itvx-text-primary;
            }
        }

    }

    &__tryAgainBtn {
        padding: $spacing-05 $spacing-09;
        max-width: 275px;
    }
}
