@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

.StartAgainOnwardJourney {
    position: absolute;
    right: 50px;
    top: 50px;
    width: 280px;
    height: 328px;
    padding: $spacing-05 $spacing-06 $spacing-06 $spacing-06;
    box-sizing: border-box;
    border-radius: $radius-02;
    background-color: $color-itvx-background-secondary;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &__heading {
        margin: $spacing-01 0 0 0;
    }

    .cp_basic-tile {
        height: 184px;

        &__body {
            margin: $spacing-04 0 0 0;
        }

        &__title {
            color: $color-itvx-text-primary;
            @include text-wrap(body, 2);
            @include itv-font-type(body, bold);
            max-height: 45px;
        }
    }

    &__button {
        margin: $spacing-01 0 0 0;
        z-index: 1;
    }
}
