@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../styles/common';

.PrivacyPolicyScreen {
    overflow: hidden;
    width: 100%;
    height: 100%;
    max-height: $app-height;

    &__content {
        margin-top: $spacing-08;
        height: 100%;
    }

    &--no-padding {
        padding: 0;
    }

    &__focusGroup {
        height: 100%;
    }

    .cp_grid,
    .cp_grid__item {
        height: 100%;
    }

    &__button {
        min-width: 100px;
    }
}
