@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

.Support {
    .Takeover__border {
        background-color: $color-itvx-background-primary;
        padding: $spacing-09;
        max-width: 496px;
    }

    .Takeover__title {
        @include itv-font-type(title-1);
        margin-bottom: $spacing-07;
    }

    .Button {
        min-width: 120px;
    }

    &__contact {
        color: $color-itvx-text-primary;
        @include itv-font-type(body-large, bold);
    }

    &__paragraph-container {
        @include itv-font-type(body-large);
        margin-bottom: $spacing-07;
    }
}
