@import "shared";
@import "~@itv/fe-components/cjs/common/styles/tokens/variables";
@import "~@itv/fe-components/cjs/common/styles/library/typography";

.samsung {
    @include video;
    @include player-overlay;
    @include slider-fix;

    .ProfileSelection,
    .Profiles {
        .cp_profile-avatar__icon {
            top: -20px;
        }

        .cp_profile-avatar__kids--large {
            top: -50px;
        }

        .cp_profile-avatar__initial {
            top: 20px;
        }
    }
}

.avplayer.internal-subtitle {
    position: absolute;
    transform: translateX(-50%);
    left: 50%;
    top: 77%;
    text-align: center;
    display: inline-block;
    height: 100%;
    width: 100%;
}

.avplayer.internal-subtitle .text {
    background-color: rgba(0, 0, 0, 0.9); /* stylelint-disable-line itv/color -- no suitable replacement from DS */
    color: $color-itvx-text-primary;
    @include itv-font-type(subtitle-1);
    padding: $spacing-03 $spacing-04 $spacing-04;
    margin: 0;
    font-weight: 700;
    display: inline;
    text-align: center;
    font-family: sans-serif; /* stylelint-disable-line itv/font-family -- non zagma font required */
    white-space: pre-wrap;
    -webkit-box-decoration-break: clone;
    -ms-box-decoration-break: clone;
    -o-box-decoration-break: clone;
    box-decoration-break: clone;
}

.avplayer.internal-subtitle .text:empty {
    display: none;
}
