@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

.VodMetadata {
    .cp_metadata {
        padding: 0 $spacing-03 $spacing-08-5 $spacing-03;

        .cp_grid__item--align-bottom {
            .cp_icon__guidance {
                width: 24px;
                height: 24px;
            }

            .cp_guidance__paragraph {
                @include itv-font-type(body-small);
                max-width: 1150px;
                white-space: pre;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }
    }

    .cp_episode-info {
        @include itv-font-type(body-small);
        padding: $spacing-04 0 $spacing-05 0;

        &__series-hierarchy {
            @include itv-font-type(body-small, bold);
        }

        .cp_icon {
            line-height: 1rem; /* stylelint-disable-line itv/typography -- override line-height for icon alignment */
            height: 100%;
        }

        &__channel-logo {
            max-height: 100%;
            width: 48px;
        }
    }
}



