@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

.Confirmation {
    .Takeover__border {
        padding: $spacing-09;
    }

    .Takeover__title {
        margin-bottom: $spacing-07;
    }

    &__container {
        max-width: 700px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    &__text {
        @include itv-font-type(body-large);
        margin-bottom: $spacing-07;
    }

    .DialogButtons {
        margin-top: 0;
    }
}
