@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '~@itv/fe-components/cjs/common/styles/tokens/variables';

$border-radius: $radius-03;
$margin: 20px;

.ScrollBar {
    position: absolute;
    top: 0;
    right: 0;
    margin: $margin;
    height: calc(100% - (#{$margin} * 2));
    width: 16px;
    border-radius: $border-radius;
    background-color: rgba($color-itvx-neutrals-100, 0.1);

    &__handle {
        width: 100%;
        position: absolute;
        background-color: $color-itvx-neutrals-100;
        border-radius: $border-radius;
    }
}
