@import 'shared';
@import 'styles/variables';
@import '../../components/ScrollableText/ScrollableText';

.youview,
.youviewsony {
    @include video;
    @include player-overlay;
    @include slider-fix;

    // HOME PAGE

    .Sponsorship {
        padding: 30px 50px 0 160px; /* stylelint-disable-line itv/spacing-within-range -- not sure on updating this value */
        display: inline-block;
    }

    // SEARCH PAGE

    .HighlightedTitle {
        &__marked {
            background-color: transparent;
        }
    }

    // NAVIGATION BAR

    .MainNav {
        position: relative;

        &__logo {
            position: absolute;
            top: $app-padding-top;
            left: $main-nav-left-spacing;
        }
    }

    // PLAYER OVERLAY

    .ScrubBar {
        &__time-remaining {
            min-width: $scrub-bar-time-remaining-width;
        }
    }

    .AdMarkerList {
        width: $scrub-bar-width - $scrub-bar-time-remaining-width;
    }

    .PlayerControl {
        display: block;
    }

    .ParentalButton {
        &.focus {
            display: flex;
        }
    }

    .ScrollableText {
        &__contentWrapper {
            height: calc(
                100% - #{$controls-height} + #{$controls-margin-top} + 2 * #{$content-wrapper-padding-vertical}
            );
        }

        &__content {
            height: 100%;
        }
    }
}
