@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/common';

.HeroFull {
    background: linear-gradient(90deg, $color-itvx-background-secondary 25%, rgba(1, 63, 86, 1) 90%);

    &__heading {
        display: inline-block;
        vertical-align: top;
        margin: 0 0 0 $spacing-07;
        @include itv-font-type(large-title);
        max-width: 700px;
    }

    &__sub-heading {
        @include itv-font-type(title-1);
        margin: 0 0 $spacing-07 0;
        max-width: 700px;
    }

    &__incentive {
        margin-top: 0;
        margin-bottom: $spacing-06;
        @include itv-font-type(title-2, bold);
        max-width: 700px;

        &--uppercase {
            text-transform: uppercase;
            white-space: nowrap;
        }
    }

    &__icon {
        margin: 0 0 $spacing-08 0;
        display: inline-block;
    }

    &__features {
        max-width: 700px;
        padding: 0;

        &-item {
            position: relative;
            list-style: none;
            padding-left: $spacing-04;
            @include itv-font-type(subtitle-1);
            margin: 0 0 $spacing-02 0;
            text-align: left;
            color: $color-itvx-text-secondary;
            max-width: 700px;
            overflow-wrap: break-word;

            &--highlight {
                @include itv-font-type(title-3);
                margin: 0 0 $spacing-08 0;
                color: $color-itvx-text-primary;
            }

            &--email {
                margin-top: $spacing-07;
                min-height: 47px;
                max-width: 700px;
                overflow-wrap: break-word;
            }
        }
    }

    &__buttons {
        position: relative;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;
        margin: 0;

        :first-child {
            margin-right: $spacing-05;
        }

        :last-child {
            margin-right: auto;
        }
    }

    &__button {
        min-width: 117px;
        min-height: 66px;
        @include itv-font-type(cta);
    }

    &__terms {
        position: relative;
        top: 100px;

        &__text {
            @include itv-font-type(body-small);
        }
    }

    &__qr-container {
        position: absolute;
        min-width: 486px;
        min-height: 486px;
        top: calc(50% - (#{$app-padding-top} / 2));
        right: 0;
        transform: translateY(-50%);
    }

    &__qr-code {
        max-width: 486px;
        max-height: 486px;
    }
}
