@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/common';

.MainNav {
    $parent: &;
    padding: $spacing-09 $spacing-03 $spacing-09 130px;
    background: linear-gradient(90deg, $color-itvx-background-secondary 5%, #FFFFFF00 90%);
    overflow: hidden;
    width: $navigation-width;
    box-sizing: border-box;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 5;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;

    &:not(.MainNav--loading) {
        &.FocusGroup--containsFocus {
            background: $color-itvx-background-secondary;
            width: 400px;
            overflow: visible;
            padding-right: 0;

            .MainNavLink__label{
                display: block;
            }

            .focus{
                &::before,  &::after{
                    display: block;
                }

                .MainNavLink__label{
                    min-width: 1px; // prevents the cutout from bouncing when menu opens/closes

                    &::before,  &::after{
                        display: block;
                    }
                }
            }
        }
    }

    &__logo {
        margin-right: auto;
        flex-grow: 0;
        display: flex;
        color: $color-itvx-brand-primary;
        width: 84px;
        left: -15px;

        &__sponsorship {
            left: 75px;
            position: absolute;
            bottom: 0;
            padding-bottom: $spacing-09;

            .cp_sponsorship__text {
                width: 100%;
                white-space: nowrap;
                margin-right: -$spacing-11;
                margin-bottom: -$spacing-03
            }

            &--is-closed { 
                .cp_sponsorship__text {
                    margin-right: 0;
                    margin-bottom: $spacing-03;
                }
            }
        }
    }

    &__moduleRoutesListItem {
        flex-grow: 1;
        display: flex;
        align-items: center;
    }

    &__list {
        align-items: center;
        display: flex;
        flex-direction: column;
        list-style-type: none;
        margin: 0;
        padding: 0;
        width: 100%;
        max-height: 90%;
        flex-basis: 90%;
        justify-content: space-between;

        li {
            position: relative;
            width: 100%;
        }
    }
}

.animations {
    .MainNav {
        transition: all $motion-duration-01 ease-out;
    }
}

// When a modal is displayed via the ModalPortal, the MainNav should be hidden.
// This is a work around for a rendering engine bug where parts of the main nav
// and page are rendered whilst the modal is displayed, FED-2570.
.App--has-modal {
    .MainNav {
        display: none;
    }
}
