@import '~@itv/fe-components/fe-components-ctv.min.css';
@import './styles/common';
@import './styles/devices/shared';

a {
    text-decoration: none;
    color: $color-itvx-text-primary;

    &:focus {
        outline: none;
    }
}

.App,
%App {
    position: absolute;
    overflow: hidden;
    width: 100%;
    height: 100%;
    @include scale-down;

    &__wrapper {
        position: relative;
        display: flex;
        flex-direction: row;
        height: 100%;
    }
}
