/* stylelint-disable itv/color -- these are set values for subtitles */
::cue(.red){ color: red; }
::cue(.green){ color: green; }
::cue(.blue){ color: blue; }
::cue(.yellow){ color: yellow; }
::cue(.cyan){ color: cyan; }
::cue(.color008000){ color: #008000; }
::cue(.background-red){background-color: red;}
::cue(.background-green){background-color: green;}
::cue(.background-blue){background-color: blue;}
::cue(.background-yellow){background-color: yellow;}
::cue(.background-cyan){background-color: cyan;}
::cue(.background-color008000){background-color: #008000;}